@import url("https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap");

.App {
  text-align: center;
}
body {
  background: url("./img/sky1.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.parinte-poza {
  display: flex;
  align-items: center;
  justify-content: left;
  margin-left: 50px;
}

.poza {
  position: absolute;
  top: 30%;
  border-radius: 20px;
  max-width: 1000px;
  box-shadow: 2px 2px 2rem rgba(6, 247, 247, 0.377);
  transition: all .4s ease-in-out;
  max-height: 80%;
}

.poza:hover {
  transform: scale(1.05);
  box-shadow: 2px 2px 3rem rgba(175, 223, 223, 0.377);
}

.titlu {
  position: absolute;
  top: 12%;
  left: 10%;
  font-size: 45px;
  color: rgb(136, 171, 236);
  padding-bottom: 3rem;
}

.link {
  display: block;
  position: absolute;
  right: 10%;
  top: 2%;
  font-size: 2rem;
  text-decoration: none;
  background: crimson;
  border-radius: 20px;
  padding: 5px;
}

.descriere {
  font-size: 21px;
  color: rgb(136, 171, 236);
  width: 400px;
  position: absolute;
  right: 10%;
  top: 10%;
  letter-spacing: 2px;
  word-spacing: 2px;
  font-family: "Lobster Two", cursive;
}

.data {
  position: absolute;
  color: rgb(136, 171, 236);
  bottom: 7%;
  right: 40%;
  font-size: 25px;
}

li a{
  text-decoration: none;
  color: black;
}

.link-home {
  display: block;
  position: absolute;
  left: 3%;
  top: 2%;
  font-size: 2rem;
  text-decoration: none;
  background: rgb(147, 20, 220);
  border-radius: 20px;
  padding: 5px;
  transition: all .4s ease-in-out;
}

.link-home:hover {
  background:rgb(220, 20, 220) ;
  transform: scale(1.1);
}


.titlu2 {
  color: rgb(128, 166, 238);
  position: absolute;
  padding-bottom: 3rem;
  left:25%;
  top: 13%;
  font-size: 2rem;
  animation: title-animate 1.5s ease-in-out forwards;
  transform: translateY(-100%);
  opacity: 0;
}



.link2 {
  display: block;
  position: absolute;
  right: 25%;
  top: 15%;
  font-size: 2rem;
  text-decoration: none;
  background: rgb(147, 20, 220);
  border-radius: 20px;
  padding: 5px;
  color: black;
  transition: all .3s ease-in-out;
  animation: animate 2s ease-in-out forwards;
  transform: translateX(100%);
  opacity: 0;
}

/* 
.link2:hover {
  background: rgb(189, 43, 226);
  transform: scale(1.1);
} */

.parinte-poza2 {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(40%);
}

.poza2 {
  border-radius: 20px;
  max-width: 100%;
  box-shadow: 2px 2px 2rem rgba(6, 247, 247, 0.377);
  transition: all .4s ease-in-out;
  max-height: 90vh;
  animation: appear 1s alternate forwards;
  transform: translateY(100%);
  opacity: 0;
}

/* 
.poza2:hover {
  transform: scale(1.2);
  box-shadow: 2px 2px 3rem rgba(175, 223, 223, 0.377);
} */

.footer {
  color: rgba(127, 255, 212, 0.418);
  position: absolute;
  bottom: 0;
  right: 0;
}


@keyframes animate{
  to {
   transform: translateX(0);
   opacity: 1;
  }
}


@keyframes appear {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}


@keyframes title-animate {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}